.cat-slides {
  .list();
  .offset_zero();
  width: 280px;
  min-height: 235px;
  margin-bottom: 27px;

  .tablet ({
    width: 708px;
    min-height: 620px;
    margin-bottom: 18px;
  });
}

.cat-slide__item {
  position: relative;
  z-index: 1;

  &--slide {
    display: none;
  }

  &--current {
    display: block;
  }
}

.cat-slide__image {
  position: absolute;
  z-index: 2;
  object-fit: contain;

  .mobile ({
    &--fat {
      width: 311px;
      height: 232px;
      left: -5px;
      object-fit: cover;
    }

    &--skinny {
      width: 545px;
      height: 520px;
    }
  });

  .tablet ({
    &--fat {
      width: 677px;
      height: 520px;
      left: 15px;
    }

    &--skinny {
      width: 545px;
      height: 520px;
      top: 5px;
      right: 56px;
    }
  });


  .desktop ({
    &--fat {
      width: 677px;
      height: 520px;
      left: 52px;
    }

    &--skinny {
      width: 545px;
      height: 520px;
      right: 20px;
    }
  });
}
