.title {
  .font-oswald(36px, 40px);

  .tablet({
    .font-oswald(60px, 60px);
  });
}

.title--green-line {
  .font-oswald(24px,26px);
  display: block;
  width: 100%;
  position: relative;
  z-index: -1;


  .tablet({
    .font-oswald(36px,36px);
    padding-left: @padding--tablet;
  });

  .desktop({
    padding-left: 74px;
  });

  &::after {
    .content();
    z-index: -2;
    //top: calc(50% + 2px);
    top: 0.65em;
    left: 0;
    right: 0;
    height: 2px;
    background-color: @basic_green;
  }
}

.title_text {
  position: relative;
  z-index: 2;
  background-color: @basic_white;
  padding-right: 22px;
  padding-left: @padding--mobile;

  .mobile ({
    background-color: @basic_white;
    display: inline-block;
    max-width: 225px;

    &--catalog {
      display: inline;
      max-width: none;
    }
  });


  .tablet({
    padding-right: 42px;
    padding-left: 28px;

    br {
      display: none;
    }
  });

  .desktop ({
    padding-left: 70px;
  });
}
